import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Header from './components/Header';
import PagOne from './pages/PagOne';
import PagTwo from './pages/PagTwo';
import PagTree from './pages/PagTree';
import Footer from './components/Footer';

function App() {
  const [selector, setSelector] = useState('Home');
  return (
    <div className="App">
      <Header changeSection={setSelector} />
      
      {/* Pagina Uno */}

      {selector === 'Home' && (
        <div>
          <PagOne />
        </div>
      )}

      {/* Pagina Two */}

      {selector === 'Referencia' && (
        <div>
          <PagTwo />
        </div>
      )}

      {/* Pagina Tree */}

      {selector === 'Publicaciones' && (
        <div>
          <PagTree />
        </div>
      )}
      <Footer />
    </div>
  );
}

export default App;
