import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';

export default function componentName(props) {
    return (
        <>
            <div className="navbarJJ sticky-top">
                <Navbar expand="lg">
                    <Navbar.Brand href="#home" className="text-light" onClick={() => props.changeSection('Home')}><svg xmlns="http://www.w3.org/2000/svg" width="2rem" height="2rem" fill="currentColor" class="bi bi-folder-check" viewBox="0 0 16 16">
                        <path d="M.5 3l.04.87a1.99 1.99 0 0 0-.342 1.311l.637 7A2 2 0 0 0 2.826 14H9v-1H2.826a1 1 0 0 1-.995-.91l-.637-7A1 1 0 0 1 2.19 4h11.62a1 1 0 0 1 .996 1.09L14.54 8h1.005l.256-2.819A2 2 0 0 0 13.81 3H9.828a2 2 0 0 1-1.414-.586l-.828-.828A2 2 0 0 0 6.172 1H2.5a2 2 0 0 0-2 2zm5.672-1a1 1 0 0 1 .707.293L7.586 3H2.19c-.24 0-.47.042-.684.12L1.5 2.98a1 1 0 0 1 1-.98h3.672z" />
                        <path d="M15.854 10.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 0 1 .708-.708l1.146 1.147 2.646-2.647a.5.5 0 0 1 .708 0z" />
                    </svg><b className="m-2">Neck</b></Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="mr-auto">
                            <Nav.Link href="#home" className="text-light" onClick={() => props.changeSection('Home')}>Inicio</Nav.Link>
                            <Nav.Link href="#link" className="text-light" onClick={() => props.changeSection('Referencia')}>Proyectos</Nav.Link>
                            <Nav.Link href="#link" className="text-light" onClick={() => props.changeSection('Publicaciones')}>Lenguajes</Nav.Link>
                        </Nav>
                        <Form inline>
                            <FormControl type="text" placeholder="Buscar" className="navbarCard m-1" />
                            <Button variant="outline-primary">Buscar</Button>
                        </Form>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        </>
    );
}
