import React from 'react'

export default function cardTwo(props) {
    return (
        <div>
            <div className="cardTwo">
                <div className="imgCardTwo">
                    <a href={props.data.src}>
                        <img className="imgCard" src={props.data.Imagen} alt="" />
                    </a>
                </div>
                <div className="RellenoCardTwo">
                    <div>
                        <p className="letrasTwoC"><b>{props.data.nombre}</b></p>
                    </div>
                    <div>
                        <p className="letrasTwo">{props.data.descripcion}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}
