import React from 'react'
import CardTwo from './CardTwo';

export default function muro() {
    // Card del Inicio
    const html = {
        Imagen: "https://www.w3c.es/Divulgacion/html/logo/img/mark-word-icon.png",
        nombre: 'Html',
        descripcion: 'Lenguaje que es el encagado de la parte escrita y basica de la web',
    }
    const css = {
        Imagen: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRqVWRyeW5dGm_WpBMCvhmbRYFgKgYKS1AAAQ&usqp=CAU",
        nombre: 'Css',
        descripcion: 'Hojas de estilo escascadas "son prara dar estilo a la pagina"',
    }
    const java = {
        Imagen: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTn83yjX_4u0jl66gX-KJ2tB6k_oAMI5br2-g&usqp=CAU",
        nombre: 'JavaScript',
        descripcion: 'Lenguaje mas utilizado en esta epoca, se lo conoce por su logica en la web',
    }
    const git = {
        Imagen: "https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png",
        nombre: 'GitHub',
        descripcion: 'Lenguaje mas utilizado para poder trabajar en grupo',
    }
    return (
        <div className="CardDisplay">
            <CardTwo data={html} />
            <CardTwo data={css} />
            <CardTwo data={java} />
            <CardTwo data={git} />
        </div>
    )
}
